@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,700,900&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  color: #ff2929;
  text-shadow: 2px;
  background-color: antiquewhite; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

h1 {
  text-align: center;
  font-weight: bolder; }

.postsPerPage {
  padding: 1em; }
  .postsPerPage select {
    color: red;
    margin-left: 0.5em;
    border: lightcoral solid 1px;
    border-radius: 3px; }
    .postsPerPage select:hover {
      cursor: pointer; }
    .postsPerPage select:focus {
      outline-color: lightcoral; }

.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s; }
  .list__item {
    display: flex;
    width: 100%;
    margin: 0.2rem;
    padding: 0.2rem 0.5rem;
    box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.185);
    -webkit-transition-duration: inherit;
            transition-duration: inherit;
    background-color: #ffe9cd; }
    .list__item--title {
      display: block;
      text-align: center;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; }
      .list__item--title::first-letter {
        text-transform: capitalize; }
    .list__item:hover {
      cursor: pointer;
      color: white;
      -webkit-transform: scale(1.03);
              transform: scale(1.03);
      background-color: lightcoral; }

.pagination {
  display: flex;
  justify-content: center;
  padding: 1em; }
  .pagination__button {
    margin: 2px;
    padding: 15px;
    line-height: 10px;
    border: 1px transparent solid;
    border-radius: 3px;
    box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.185); }
    .pagination__button:hover {
      cursor: pointer;
      -webkit-transform: scale(1.03);
              transform: scale(1.03);
      border-color: lightcoral; }
    .pagination__button--active {
      color: white;
      background-color: lightcoral; }

.myApp {
  display: flex;
  flex-direction: column;
  align-items: center; }

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: lightcoral transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s; }

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

